import {
  AfterViewInit,
  Component,
  EventEmitter,
  Input,
  OnChanges,
  OnInit,
  Output,
  ViewChild,
  ViewEncapsulation, ElementRef
} from '@angular/core';

import {Option} from './option';
import {OptionList} from './option-list';



export class SelectDropdownComponent
  implements AfterViewInit, OnChanges, OnInit {

   filterEnabled: boolean;
   highlightColor: string;
   highlightTextColor: string;
   left: number;
   multiple: boolean;
   notFoundMsg: string;
   optionList: OptionList;
   top: number;
   width: number;
   placeholder: string;

   close = new EventEmitter<boolean>();
   optionClicked = new EventEmitter<Option>();
   singleFilterClick = new EventEmitter<null>();
   singleFilterInput = new EventEmitter<string>();
   singleFilterKeydown = new EventEmitter<any>();

   filterInput: any;
   optionsList: any;

  disabledColor = '#fff';
  disabledTextColor = '9e9e9e';

  constructor(private _elementRef: ElementRef) {}

  /** Event handlers. **/

  // Angular life cycle hooks.

  ngOnInit() {
    this.optionsReset();
  }

  ngOnChanges(changes: any) {
    if (changes.hasOwnProperty('optionList')) {
      this.optionsReset();
    }

    const container = this._elementRef.nativeElement.classList;
    setTimeout( () => { container.add('fadeInSelect'); }, 200 );
  }

  ngAfterViewInit() {
    this.moveHighlightedIntoView();
    if (!this.multiple && this.filterEnabled) {
      this.filterInput.nativeElement.focus();
    }
  }

  // Filter input (single select).

  onSingleFilterClick(event: any) {
    this.singleFilterClick.emit(null);
  }

  onSingleFilterInput(event: any) {
    this.singleFilterInput.emit(event.target.value);
  }

  onSingleFilterKeydown(event: any) {
    this.singleFilterKeydown.emit(event);
  }

  // Options list.

  onOptionsWheel(event: any) {
    this.handleOptionsWheel(event);
  }

  onOptionMouseover(option: Option) {
    this.optionList.highlightOption(option);
  }

  onOptionClick(option: Option) {
    this.optionClicked.emit(option);
  }

  /** Initialization. **/

  private optionsReset() {
    this.optionList.filter('');
    this.optionList.highlight();
  }

  /** View. **/

  getOptionStyle(option: Option): any {
    if (option.highlighted) {
      const style: any = {};

      if (typeof this.highlightColor !== 'undefined') {
        style['background-color'] = this.highlightColor;
      }
      if (typeof this.highlightTextColor !== 'undefined') {
        style['color'] = this.highlightTextColor;
      }
      return style;
    } else {
      return {};
    }
  }

  clearFilterInput() {
    if (this.filterEnabled) {
      this.filterInput.nativeElement.value = '';
    }
  }

  moveHighlightedIntoView() {

    const list = this.optionsList.nativeElement;
    const listHeight = list.offsetHeight;

    const itemIndex = this.optionList.getHighlightedIndex();

    if (itemIndex > -1) {
      const item = list.children[0].children[itemIndex];
      const itemHeight = item.offsetHeight;

      const itemTop = itemIndex * itemHeight;
      const itemBottom = itemTop + itemHeight;

      const viewTop = list.scrollTop;
      const viewBottom = viewTop + listHeight;

      if (itemBottom > viewBottom) {
        list.scrollTop = itemBottom - listHeight;
      } else if (itemTop < viewTop) {
        list.scrollTop = itemTop;
      }

    }
  }

  private handleOptionsWheel(e: any) {
    const div = this.optionsList.nativeElement;
    const atTop = div.scrollTop === 0;
    const atBottom = div.offsetHeight + div.scrollTop === div.scrollHeight;

    if (atTop && e.deltaY < 0) {
      e.preventDefault();
    } else if (atBottom && e.deltaY > 0) {
      e.preventDefault();
    }

  }

static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: 'mdb-select-dropdown',
  templateUrl: 'select-dropdown.component.html',
  encapsulation: ViewEncapsulation.None
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: ElementRef, },
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'filterEnabled': [{ type: Input },],
'highlightColor': [{ type: Input },],
'highlightTextColor': [{ type: Input },],
'left': [{ type: Input },],
'multiple': [{ type: Input },],
'notFoundMsg': [{ type: Input },],
'optionList': [{ type: Input },],
'top': [{ type: Input },],
'width': [{ type: Input },],
'placeholder': [{ type: Input },],
'close': [{ type: Output },],
'optionClicked': [{ type: Output },],
'singleFilterClick': [{ type: Output },],
'singleFilterInput': [{ type: Output },],
'singleFilterKeydown': [{ type: Output },],
'filterInput': [{ type: ViewChild, args: ['filterInput', ] },],
'optionsList': [{ type: ViewChild, args: ['optionsList', ] },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
